'use client'

import { useEffect, useState } from 'react'
import { getTopAnime, getSeasonNow, getPopularAnime, Anime } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { AnimeSkeletonGrid } from '@/components/skeleton-loader'
import Link from 'next/link'
import { ArrowRight, Star, TrendingUp, Calendar as CalendarIcon, PlayCircle } from 'lucide-react'
import Image from 'next/image'

export default function Home() {
  const [loading, setLoading] = useState(true)
  const [topAnime, setTopAnime] = useState<Anime[]>([])
  const [seasonAnime, setSeasonAnime] = useState<Anime[]>([])
  const [popularAnime, setPopularAnime] = useState<Anime[]>([])

  useEffect(() => {
    async function fetchData() {
      setLoading(true)
      try {
        const top = await getTopAnime()
        const season = await getSeasonNow()
        const popular = await getPopularAnime()
        setTopAnime(top.data)
        setSeasonAnime(season.data)
        setPopularAnime(popular.data)
      } catch (e) {
        console.error(e)
      } finally {
        setLoading(false)
      }
    }
    fetchData()
  }, [])

  if (loading) return <SkeletonGrid />

  const heroAnimes: Anime[] = topAnime.slice(0, 2)
  const sideList: Anime[] = topAnime.slice(2, 7)
  const seasonalAnime: Anime[] = seasonAnime.slice(0, 12)
  const popularAnimeList: Anime[] = popularAnime.slice(0, 12)

  const TopChartsList = ({ sideList }: { sideList: Anime[] }) => (
    <div className="flex flex-col gap-3">
      {sideList.map((anime: Anime, idx: number) => (
        <Link
          key={anime.mal_id}
          href={`/anime/${anime.mal_id}`}
          className="flex gap-4 items-center p-3 rounded-xl bg-card hover:bg-secondary/40 border border-border/50 hover:border-border transition-all group"
        >
          <span className="text-xl font-black w-6 text-center text-muted-foreground group-hover:text-primary transition-colors">
            {idx + 3}
          </span>
          <div className="relative w-12 h-16 rounded-md overflow-hidden bg-muted shadow-sm border border-border/50">
            <Image
              src={anime.images.jpg.image_url}
              alt={anime.title}
              fill
              className="object-cover group-hover:scale-110 transition-transform"
            />
          </div>
          <div className="flex-1 min-w-0">
            <h4 className="text-sm font-semibold truncate group-hover:text-primary transition-colors">
              {anime.title}
            </h4>
          </div>
        </Link>
      ))}
    </div>
  )

  return (
    <main className="min-h-screen bg-background text-foreground">
      <Navbar />

      {/* HERO & TRENDING */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 md:py-12">
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
          <div className="lg:col-span-8 flex flex-col gap-6">
            {heroAnimes.map((anime, index) => (
              <div
                key={anime.mal_id}
                className="relative flex flex-col sm:flex-row bg-card rounded-xl overflow-hidden border border-border/50 shadow-sm hover:shadow-lg transition-all group h-full"
              >
                <div className="relative w-full sm:w-5/12 aspect-[16/9] sm:aspect-auto shrink-0 h-56 sm:h-auto overflow-hidden">
                  <Image
                    src={anime.images.jpg.large_image_url}
                    alt={anime.title}
                    fill
                    className="object-cover transition-transform duration-700 group-hover:scale-105"
                    priority={index === 0}
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/80 dark:from-black/90 via-black/40 dark:via-black/20 to-transparent" />
                  <div className="absolute top-3 left-3 flex items-center gap-1 text-xs font-semibold px-3 py-1 rounded-full bg-card/80 text-foreground border border-border/50 backdrop-blur-md shadow hidden sm:flex">
                    <TrendingUp size={14} />
                    #{index + 1} Trending
                  </div>
                </div>
                <div className="flex flex-col p-5 sm:w-7/12 flex-grow h-full">
                  <div className="flex flex-col flex-grow">
                    <div className="mb-3">
                      <div className="flex items-center gap-1 text-xs font-semibold px-3 py-1 rounded-full bg-card/80 text-foreground border border-border/50 backdrop-blur-md shadow inline-flex sm:hidden mb-2">
                        <TrendingUp size={14} /> #{index + 1} Trending
                      </div>
                      <div className="flex items-center gap-2 text-[10px] font-bold text-primary uppercase tracking-wider mb-1">
                        <span>{anime.type}</span>
                      </div>
                      <Link href={`/anime/${anime.mal_id}`} className="block">
                        <h1 className="text-xl md:text-2xl font-extrabold leading-tight hover:text-primary transition-colors line-clamp-2">
                          {anime.title}
                        </h1>
                      </Link>
                    </div>
                    <div className="flex flex-wrap items-center gap-3 text-xs text-muted-foreground mb-3">
                      <span className="flex items-center gap-1 font-bold text-yellow-500 dark:text-yellow-400">
                        <Star className="fill-yellow-500 dark:fill-yellow-400" size={14} />
                        {anime.score}
                      </span>
                      <span>•</span>
                      <span className="flex items-center gap-1">
                        <CalendarIcon size={14} /> {anime.year || 'TBA'}
                      </span>
                      <span>•</span>
                      <span className="flex items-center gap-1">
                        <PlayCircle size={14} /> {anime.episodes || '?'} Eps
                      </span>
                    </div>
                    <p className="text-muted-foreground line-clamp-3 leading-relaxed text-sm mb-3">
                      {anime.synopsis}
                    </p>
                  </div>
                  <div className="pt-2 mt-auto">
                    <Link
                      href={`/anime/${anime.mal_id}`}
                      className="inline-flex h-9 items-center justify-center rounded-md border border-input bg-background/60 backdrop-blur-sm px-6 text-xs font-bold text-foreground shadow-sm hover:bg-primary hover:text-primary-foreground transition-colors"
                    >
                      View Details
                    </Link>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* RIGHT SIDEBAR */}
          <div className="hidden lg:col-span-4 lg:flex flex-col h-full">
            <div className="flex items-center justify-between mb-4 px-1">
              <h3 className="font-bold text-lg flex items-center gap-2">
                <TrendingUp className="text-primary" size={18} /> Top Charts
              </h3>
              <Link href="/trending" className="text-xs text-muted-foreground hover:text-primary transition-colors">
                View All
              </Link>
            </div>
            <TopChartsList sideList={sideList} />
          </div>
        </div>
      </div>

      {/* SEASONAL */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pb-12 space-y-16">
        <section>
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl md:text-3xl font-bold">Season Now</h2>
            <Link href="/seasonal" className="text-sm font-medium text-primary hover:text-primary/80 flex items-center gap-1">
              View All <ArrowRight size={16} />
            </Link>
          </div>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 md:gap-6">
            {seasonalAnime.map(anime => <AnimeCard key={anime.mal_id} anime={anime} />)}
          </div>
        </section>

        <section>
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl md:text-3xl font-bold">All Time Popular</h2>
            <Link href="/popular" className="text-sm font-medium text-primary hover:text-primary/80 flex items-center gap-1">
              View All <ArrowRight size={16} />
            </Link>
          </div>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 md:gap-6">
            {popularAnimeList.map(anime => <AnimeCard key={anime.mal_id} anime={anime} />)}
          </div>
        </section>
      </div>

      {/* FOOTER */}
      <footer className="border-t border-border bg-card mt-auto">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-10 flex flex-col md:flex-row justify-between items-center gap-8">
          <div className="text-center md:text-left"><span className="font-bold text-xl">Feinime</span></div>
          <nav className="flex flex-wrap justify-center gap-4 text-sm text-muted-foreground">
            {['Home', 'Popular', 'Trending', 'About', 'Contact', 'FAQ'].map(item => (
              <a key={item} href="#" className="hover:text-primary transition-colors">{item}</a>
            ))}
          </nav>
        </div>
      </footer>
    </main>
  )
}
